import { Component, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { TrooperConfig } from '../battle/trooper/trooper.component';

const CLASSES: any[] = [
  'peasant',
  'knight',
  'soldier',
  'warlock',
  'mage',
  'king',
];

const WEAPONS: any[] = ['sword', 'stick', 'staff', 'spear'];

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
})
export class HomeComponent implements OnInit {
  characters$: BehaviorSubject<{
    good: TrooperConfig;
    evil: TrooperConfig;
  }> = new BehaviorSubject(null);
  results: string[] = [];

  constructor() {}

  ngOnInit() {}

  onPlay() {
    this.characters$.next({
      good: {
        race: 'human',
        animation: 'stand',
        class: CLASSES[Math.floor(Math.random() * CLASSES.length)],
        direction: 'right',
        weapon: WEAPONS[Math.floor(Math.random() * WEAPONS.length)],
      },
      evil: {
        race: 'ghoul',
        animation: 'stand',
        class: CLASSES[Math.floor(Math.random() * CLASSES.length)],
        direction: 'left',
        weapon: WEAPONS[Math.floor(Math.random() * WEAPONS.length)],
      },
    });
  }

  onResult(winner: string) {
    this.results.push(winner);
  }
}
