import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { TrooperConfig } from './trooper/trooper.component';
import { Observable, BehaviorSubject } from 'rxjs';

function timeout(ms) {
  return new Promise((resolve) => setTimeout(resolve, ms));
}

@Component({
  selector: 'app-battle',
  templateUrl: './battle.component.html',
  styleUrls: ['./battle.component.css'],
})
export class BattleComponent implements OnInit {
  fighting: boolean;
  winEvil: boolean;
  winGood: boolean;
  goodTrooperConfig: TrooperConfig;
  evilTrooperConfig: TrooperConfig;

  @Input() characters$: BehaviorSubject<{
    good: TrooperConfig;
    evil: TrooperConfig;
  }>;
  @Output() end = new EventEmitter();

  ngOnInit() {
    this.characters$.subscribe((params) => {
      console.log(params);
      if (params && !this.fighting) {
        this.goodTrooperConfig = params.good;
        this.evilTrooperConfig = params.evil;
        this.winGood = false;
        this.winEvil = false;
        this.fight();
      }
    });
  }

  onEnd() {
    if (this.winGood) {
      this.end.emit('The good guys have won');
    } else {
      this.end.emit('The bad guys have won');
    }

    this.fighting = false;
  }

  private async attack() {
    this.goodTrooperConfig.animation = 'attack';
    this.evilTrooperConfig.animation = 'attack';
    await timeout(100);
    this.goodTrooperConfig.animation = 'stand';
    this.evilTrooperConfig.animation = 'stand';
    await timeout(100);
  }

  private async fight() {
    this.fighting = true;
    const youWin = Math.floor(Math.random() * 2) + 1;

    await this.attack();
    await this.attack();
    await this.attack();
    await this.attack();
    await this.attack();

    if (youWin === 1) {
      this.winGood = true;
      this.winEvil = false;
      this.goodTrooperConfig.animation = 'stand';
      this.evilTrooperConfig.animation = 'dead';

      await timeout(500);
      this.goodTrooperConfig.animation = 'win';
    } else {
      this.winGood = false;
      this.winEvil = true;
      this.goodTrooperConfig.animation = 'dead';
      this.evilTrooperConfig.animation = 'stand';

      await timeout(500);
      this.evilTrooperConfig.animation = 'win';
    }
  }
}
